function random (min, max) {
  const range = max - min
  const rand = Math.random()
  const num = min + Math.round(rand * range)
  return num
}
function getArray (n) {
  const rs = []
  for (let i = 0; i < n; i++) {
    rs.push(random(0, 20))
  }
  return rs
}
function sum (arr) {
  return arr.reduce(function (prev, curr, idx, arr) {
    return prev + curr
  })
}
function getData () {
  let arr = JSON.parse(localStorage.getItem('rdmArray'))
  if (!arr) {
    arr = getArray(300)
    localStorage.setItem('rdmArray', JSON.stringify(arr))
  }
  return { arr, sum: sum(arr) }
}
export const rdmArray = getData()
