<template>
  <div class="secondLive">
    <img
      src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/callback/1.png"
      alt="" />
    <div class="form">
      <div class="content">
        <VanForm
          @submit="onSubmit"
          @failed="showErrorMsg"
          ref="form"
          validate-first
          :show-error="false"
          :show-error-message="false">
          <VanField
            v-model="form.phone"
            name="phone"
            placeholder="请填写手机号"
            :rules="[
              { required: true, message: '33003' },
              {
                pattern: phoneReg,
                message: '33004',
              },
            ]">
            <template #left-icon>
              <span class="prefix"> +86 <span class="icon">▼</span> </span>
            </template>
          </VanField>
          <div class="wrap">
            <VanField
              class="phonecode"
              v-model="form.code"
              name="code"
              placeholder="请填写验证码"
              :rules="[{ required: true, message: '33005' }]">
            </VanField>
            <div class="code">
              <CountDown :before-send="beforeSend"></CountDown>
            </div>
          </div>
          <!-- <div class="tips">
            直播课<span class="red">0元</span>名额还剩<span class="red">{{
              nums
            }}</span>人<span
              v-if="nums > 0">，即将恢复<span class="red">199元</span>原价</span>
          </div> -->
        </VanForm>
      </div>
    </div>
    <img src="@/assets/callback/bg.jpg" alt="" />
    <div
      class="submit"
      v-if="sum <= 0"
      @click="submit">
      <div class="left">
        <div class="text">抢先体验<span class="big">微淼实践班</span></div>
        <div class="price">仅需<span class="num">199</span>元</div>
      </div>
      <div class="right">
        立即报名 <img
          class="icon"
          src="@/assets/callback/next.png"
          alt="" />
      </div>
    </div>
    <Footer
      v-if="sum > 0"
      @click="submit"
      :nums="sum" />
    <VanSwipe
      class="swipe"
      :show-indicators="false"
      vertical
      :autoplay="3000">
      <VanSwipeItem v-for="(item, key) in swiperList" :key="key">
        <img
          :src="item.portrait"
          alt=""
          class="icon" />
        <div class="text">{{ item.phone }} {{ item.time }}秒前 报名成功</div>
      </VanSwipeItem>
    </VanSwipe>
    <PayTypeDialog @change="pay" v-model="showPaySelet"></PayTypeDialog>
    <VanDialog
      :show-confirm-button="false"
      :overlay-style="{backgroundColor:maskColor}"
      v-model="showMpDialog">
      <Dialog :options="reportData" :src="src">
        <span v-html="dialogTitle"></span>
        <span slot="des" v-html="dialogDes"></span>
      </Dialog>
    </VanDialog>
  </div>
</template>
<script>
import { getSearch } from '@/utils'
import PayTypeDialog from '@/components/common/SelectPayType' // 支付方式选择弹窗
import CountDown from '@/components/common/CountDown'
import Footer from '../components/footer'
import Dialog from '../components/Dialog.vue'
import { codeMap } from './ErrorMsg.js'
import { rdmArray } from './getRoudomArray.js'
import Api from '@/api/apis'
export default {
  components: {
    PayTypeDialog,
    CountDown,
    Footer,
    Dialog
  },
  data: function () {
    return {
      swiperList: [],
      form: {},
      isFinsh: true,
      showPaySelet: false,
      sum: 0,
      text: 0,
      showMpDialog: false,
      showBtnDialog: true,
      dialogTitle: '',
      dialogDes: '',
      reportData: {},
      maskColor: 'rgba(0,0,0,0.7)',
      src: 'https://cdn-xiaobai-market.weimiaocaishang.com/Public/xun/qrcode/weimiaoxuetang.png'
    }
  },
  async beforeRouteEnter (to, from, next) {
    if (localStorage.getItem('uid_recall')) return next()
    const { wm_click_id: id } = await Api.user.newClickId()
    localStorage.setItem('uid_recall', id)
    next()
  },
  computed: {
    nums () {
      return this.sum > 0 ? this.sum : 0
    }
  },
  async created () {
    this.form = {
      phone: localStorage.getItem('callback-phone'),
      code: localStorage.getItem('callback-code')
    }
    this.initLog()
    this.$log.show({
      module_ori: '短信→H5分班入口-浏览器H5落地页访问',
      module_part: '107_h5_xbzb_formal__hyj_0045'
    })
    // 获取最新报名客户
    const data = await this.$apis.userOperator.getLatestUser()
    this.swiperList = data
    if (!this.form.phone) return
    this.needCheck = !!getSearch('checkOrder')
    this.changeQuery('checkOrder')
    this.checkOrder(true)
    this.timer = setInterval(() => {
      this.checkOrder()
    }, 3000)
  },
  mounted () {
    this.contInterval()
    this.$toast.setDefaultOptions({
      className: 'secondLiveToast'
    })
    this.heartbeat = this.$log.heartbeat(
      {
        event: 'heart',
        module_ori: '短信→H5分班入口-浏览器H5落地页访问时长',
        module_part: '107_h5_xbzb_formal__hyj_0046'
      },
      5000,
      1
    )
    this.$log.show({
      module_part: '107_h5_xbzb_formal__hyj_0047',
      module_ori: '短信→H5分班入口-浏览器H5落地页访问长度'
    })
    this.heartbeat.start()
  },
  beforeDestroy () {
    this.stop()
  },
  methods: {
    async initLog () {
      const query = getSearch()
      const uid = localStorage.getItem('uid_recall')
      this.$log.update({
        alternate: JSON.stringify({
          message_touch: 1,
          message_id: query.message_id,
          activity_id: query.activity_id,
          uid_recall: uid,
          uv4recall: '1',
          screen_loc: 2
        }),
        current_page: location.href
      })
    },
    contInterval () {
      const sum = rdmArray.sum
      const firtSeconds = Date.parse(new Date()) / 1000
      const lastSeconds = localStorage.getItem('firtSeconds')
      let time = 0
      if (!lastSeconds) {
        localStorage.setItem('firtSeconds', firtSeconds)
        time = 300
      } else {
        time = 300 - (firtSeconds - lastSeconds)
      }
      const arr = rdmArray.arr
        .filter((item, index) => {
          return index > time
        })
        .reduce(function (prev, curr, idx, arr) {
          return prev + curr
        }, 0)

      this.sum = sum - arr
      this.timer1 = setInterval(() => {
        if (this.sum <= 0) {
          clearInterval(this.timer1)
          this.text = 199
          this.sum = 0
          return
        }
        time--
        this.sum = this.sum - (rdmArray.arr[time] || 1)
      }, 1000)
    },
    stop () {
      clearInterval(this.timer)
      clearInterval(this.timer1)
      this.heartbeat.stop()
    },
    // 提交表单
    submit () {
      let data = {
        module_ori: '短信→H5分班入口-浏览器H5落地页-点我分班按钮-0元课',
        module_part: '107_h5_xbzb_formal__hyj_0048'
      }
      if (this.sum <= 0) {
        data = {
          module_ori: '短信→H5分班入口-浏览器H5落地页-点我分班按钮-199元',
          module_part: '107_h5_xbzb_formal__hyj_0051'
        }
      }
      this.$log.click(data)
      this.$refs.form.submit()
    },
    ZeroSubmit () {
      const query = getSearch()
      const uid = localStorage.getItem('uid_recall')
      this.$apis.userOperator
        .signUp({
          message_id: query.message_id,
          activity_id: query.activity_id,
          ...this.form,
          ...query,
          uid_recall: uid
        })
        .then((res) => {
          this.goSucccess()
        })
        .catch(err => {
          this.showMsg(err)
        })
    },
    // 弹出提示
    showMsg (err) {
      this.$dialog.close()
      const { msg, code } = err
      console.log(msg, codeMap)
      this.reportData = codeMap[code].report
      // 39100  已经报名
      if (code === 39100) {
        // 查看老师二维码
        this.$dialog({
          title: '您已报名该课程',
          theme: 'round-button',
          confirmButtonText: '查看老师'
        }).then(res => {
          this.$log.click(this.reportData)
          this.goSucccess()
        })
        return
      }
      if ([39103, 39101].includes(code)) {
        // 不在人群包 名额已满 //引导公众号
        // 弹框不透明度设置1
        this.maskColor = 'rgba(0,0,0,1)'
        this.dialogTitle = '抱歉～<br/> 课程太火爆了, 名额已满'
        this.dialogDes = '小淼提醒您关注《微淼学堂》公众号<br/>了解下期活动开启时间'
        this.showMpDialog = true
        this.src = 'https://cdn-xiaobai-market.weimiaocaishang.com/Public/xun/qrcode/weimiaoxuetang.png'
        return
      }
      if (code === 39102) {
        // 超出活动时间
        this.dialogTitle = '本期活动已结束'
        this.dialogDes = '小淼提醒您关注《微淼学堂》公众号 <br/> 了解下期活动开启时间'
        this.showMpDialog = true
        this.src = 'https://cdn-xiaobai-market.weimiaocaishang.com/Public/xun/qrcode/weimiaoxuetang.png'
        return
      }
      if ([30007, 39104].includes(code)) {
        this.dialogTitle = '您已经学过该课程啦'
        this.dialogDes = '小淼向您推荐《微淼大咖课》 <br/>每周一位理财大咖，直播分享理财真经'
        this.showMpDialog = true
        this.src = 'https://cdn-xiaobai-market.weimiaocaishang.com/Public/xun/qrcode/weimiaodaka.png'
        return
      }
      if (code === 39105) {
        // 购买小课程
        this.$dialog({
          message: '该课程为进阶课程，\n小淼建议您先学习基础课程',
          theme: 'round-button',
          confirmButtonText: '立即报名'
        }).then(res => {
          this.$log.click(this.reportData)
          location.href = 'https://info.weimiaoxuetang.top/customer/?site_id=152#/Scene/LiveEnhance12?from=wm'
        })
        return
      }
      if (code === 39106) {
        // 已经购买大课
        this.dialogTitle = '抱歉～ <br/> 该课程仅适合小白学习'
        this.dialogDes = '小淼向您推荐《微淼大咖课》 <br/> 每周一位理财大咖，直播分享理财真经'
        this.showMpDialog = true
        this.src = 'https://cdn-xiaobai-market.weimiaocaishang.com/Public/xun/qrcode/weimiaodaka.png'
        return
      }
      this.$toast(msg || codeMap[code].msg)
    },
    // 跳转成功页面
    goSucccess () {
      this.$router.push({
        name: 'SecondLiveResult',
        query: {
          ...this.$route.query
        }
      })
    },
    // 查询订单
    checkOrder (isClear) {
      const phone = localStorage.getItem('callback-phone')
      if (!phone) return
      const query = getSearch()
      if (!query.activity_id) return
      if (!query.message_id) return
      this.$apis.userOperator
        .checkActiveUser({
          phone: this.form.phone,
          message_id: query.message_id,
          activity_id: query.activity_id
        })
        .catch((err) => {
          // 支付成功后
          if (err.code === 39100 && this.needCheck) {
            this.goSucccess()
            return
          }
          this.showMsg(err)
          clearInterval(this.timer)
          if (isClear && err.code !== 39100) {
            this.setPhone()
          }
        })
    },
    setPhone () {
      localStorage.removeItem('callback-phone')
      localStorage.removeItem('callback-code')
      this.form = {
        phone: '',
        code: ''
      }
    },
    // 获取验证码
    beforeSend (start) {
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 表单验证错误
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    // 表单校验失败
    onSubmit (e) {
      // 校验手机号和验证码
      const query = getSearch()
      localStorage.setItem('callback-phone', this.form.phone)
      localStorage.setItem('callback-code', this.form.code)
      this.$apis.payment
        .payBeforeCheck({
          ...this.form,
          message_id: query.message_id,
          activity_id: query.activity_id,
          product_sn: 'PRODUCT_SN_XIAOBAI_199_LIVE'
        })
        .then(async (res) => {
          if (this.sum > 0) {
            return this.ZeroSubmit()
          }
          this.showPaySelet = true
        })
        .catch((err) => {
          this.$dialog.close()
          if (err.code === 30001) {
            return this.goSucccess()
          }
          if (err.code === 33002) {
            this.showMsg(err)
            return
          }
          this.checkOrder()
        })
    },
    // 支付
    pay (payType) {
      if (payType) {
        localStorage.setItem('payType', payType)
      }
      payType = localStorage.getItem('payType')
      const query = getSearch()
      const uid = localStorage.getItem('uid_recall')
      const url = location.href + '&checkOrder=yes'
      this.$apis.payment
        .pay({
          ...query,
          ...this.form,
          pay_type: payType,
          return_url: encodeURIComponent(url),
          product_sn: 'PRODUCT_SN_XIAOBAI_199_LIVE',
          uid_recall: uid
        })
        .then((data) => {
          if (payType === '1') {
            this.aliPay(data)
          }
          if (payType === '2') {
            this.wxPay(data)
          }
        })
        .catch((err) => {
          console.log(err)
          if (err.code === 33002) {
            this.showMsg(err)
            return
          }
          this.checkOrder()
        })
    },
    // 微信支付
    wxPay (data) {
      window.location.replace(decodeURIComponent(data.url))
    },
    // 支付宝支付
    aliPay (data) {
      this.changeQuery('checkOrder', 'yes')
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) {
        isReSet.parentNode.removeChild(isReSet)
      }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      div.children[0].submit()
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.replace({
        ...this.$route,
        query: {
          ...query
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.secondLive {
  padding-bottom: 110px;
  /deep/ .test .van-dialog--round-button .van-dialog__confirm {
    width: 490px;
    height: 90px;
    background: linear-gradient(-90deg, #fe2d2a, #fe612d);
    border-radius: 10px;
  }

  @mixin baseBtn {
    width: 377px;
    height: 110px;
  }

  .swipe {
    height: 100px;
    position: fixed;
    width: 420px;
    height: 66px;
    bottom: 160px;
    right: 30px;
    background: #000;
    opacity: 0.7;
    border-radius: 33px;
    color: #fff;

    .van-swipe-item {
      display: flex;
      align-items: center;
    }

    .icon {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      margin-right: 15px;
      margin-left: 20px;
    }
  }

  .submit {
    position: fixed;
    bottom: 0;
    width: 750px;
    height: 120px;
    font-family: Source Han Sans CN;
    display: flex;
    align-items: center;

    .left {
      flex: 1;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      background: linear-gradient(0deg, #ff481f, #ff3319);
      border-radius: 15px 0 0 0;
      height: 100%;
      font-size: 26px;
      font-family: PingFang SC;
      font-weight: 400;
      font-style: italic;
      color: #fff;

      .big {
        font-size: 28px;
      }

      .price {
        font-size: 28px;

        .num {
          font-size: 44px;
        }
      }
    }

    .right {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      background: linear-gradient(-90deg, #ffa516, #ffbd0b);
      border-radius: 0 15px 0 0;
      font-size: 60px;
      font-family: Source Han Sans CN;
      font-weight: 800;
      color: #d91a01;

      .icon {
        width: 25px;
        margin-left: 30px;
      }
    }
  }

  img {
    width: 100%;
    vertical-align: bottom;
  }

  .form {
    background: #eb2726;
    padding: 0 30px;

    .tips {
      // width: 560px;
      height: 27px;
      font-size: 26px;
      font-family: Source Han Sans CN;
      font-weight: 400;
      color: #333;
      line-height: 41px;

      .red {
        color: #f3422f;
      }
    }

    .content {
      height: 361px;
      background: #fffaf4;
      box-shadow: 0 6px 20px 0 rgba(166, 47, 29, 0.35);
      border-radius: 10px;
      display: flex;
      justify-content: center;
      align-items: center;

      .wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 30px;
      }

      .code {
        display: flex;
        width: 229px;
        height: 86px;
        background: #f3422f;
        border-radius: 7px;
        font-size: 28px !important;
        font-family: Source Han Sans CN;
        font-weight: 400;
        color: #fffaf4;
        justify-content: center;
        align-items: center;
      }

      /deep/ .van-cell {
        &.phonecode {
          width: 332px;
          margin-bottom: 0;
        }

        .prefix {
          font-size: 32px;
          font-weight: 500;
          color: #333;
          display: flex;
          align-items: center;

          .icon {
            font-size: 25px;
            margin-left: 10px;
            color: #999;
          }

          padding-right: 15px;
        }

        margin-bottom: 30px;
        width: 610px;
        height: 86px;
        background: #fff;
        border: 1px solid #d6d6d6;
        border-radius: 6px;
        font-size: 30px;
        font-family: SourceHanSansSC;
        font-weight: 500;
        color: #999;

        &::after {
          display: none;
        }
      }
    }
  }
}
</style>
